.data_start {
  margin-top: 300px;
}

.data {
  width: 750px;
  margin: auto;
}

/* ------------------- Links ----------------- */
.links {
  margin-bottom: 80px;
}

/* ------------------ About ------------- */
.about {
  margin-bottom: 80px;
  padding: 35px;
  background-color: rgb(0 0 0 / 24%);
}
.about > p {
  text-align: justify;
  font-size: 20px;
}

.about > h4 {
  font-size: 30px;
  font-weight: 600;
}

/* ----------------------- Skills ------------------------ */

.skills {
  margin-bottom: 80px;
}
.skills > p {
  text-align: justify;
  font-size: 20px;
}

.skills > h4 {
  font-size: 30px;
  font-weight: 600;
}
.skills > span {
  padding: 15px 20px;
  font-size: 15px;
}

/*  -------------------- Personal Projects ---------------- */
.projects {
  margin-bottom: 80px;
}
.projects > h4 {
  font-size: 30px;
  font-weight: 600;
}

/* --------------------------- education ---------------------- */
.education > h4 {
  font-size: 30px;
  font-weight: 600;
}

.vr1 {
  height: 60px;
  margin-left: 15%;
  width: 3px;
  opacity: 1;
  border-radius: 3px;
  color: #0d6efd;
}

@media screen and (max-width: 760px) {
  .data {
    width: 100%;
    margin: auto;
  }
}