#navbar {
  overflow: hidden;
  background-color: #00000073;
  /* padding: 24px 50px; */
  padding: 50px 50px;
  transition: 0.4s;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 99;
}

.profile_pic {
  border-radius: 50%;
  width: 15%;
  border: 2px solid white;
  transition: 0.4s;
}

#navbar-right > h4 {
  font-size: 35px;
}

#navbar-right > h5 {
  font-size: 18px;
  font-weight: 600;
  color: rgb(49, 48, 48);
}
#navbar-right > h6 > span {
  font-size: 19px;
  font-weight: 600;
  color: rgb(19, 68, 124);
}

#navbar-right > h6 {
  font-size: 17px;
  font-weight: 600;
  color: rgb(19, 68, 124);
}

/* --------------------Shrink Navbar CSS------------- */
#navbar_shrink {
  overflow: hidden;
  background-color: #00000073;
  padding: 10px 25px;
  transition: 0.4s;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 99;
}

.profile_pic_shrink {
  border-radius: 50%;
  width: 10%;
  border: 2px solid white;
  transition: 0.4s;
}

#navbar-right_shrink > h4 {
  font-size: 25px;
}

#navbar-right_shrink > h5 {
  font-size: 14px;
  font-weight: 600;
  color: rgb(49, 48, 48);
}
#navbar-right_shrink > h6 > span {
  font-size: 15px;
  font-weight: 600;
  color: rgb(19, 68, 124);
}

#navbar-right_shrink > h6 {
  font-size: 14px;
  font-weight: 600;
  color: rgb(19, 68, 124);
}

@media screen and (max-width: 580px) {
  #navbar {
    padding: 20px 10px !important;
  }
  #navbar-right {
    float: none;
  }
}

@media screen and (max-width: 920px) {
  .profile_pic_shrink {
    border-radius: 50%;
    width: 12%;
    border: 2px solid white;
    transition: 0.4s;
  }
}

@media screen and (max-width: 760px) {
  .profile_pic {
    border-radius: 50%;
    width: 21%;
    border: 2px solid white;
    transition: 0.4s;
  }
  .profile_pic_shrink {
    border-radius: 50%;
    width: 14%;
    border: 2px solid white;
    transition: 0.4s;
  }
  #navbar-right_shrink > h4 {
    font-size: 22px;
  }

  #navbar-right_shrink > h5 {
    font-size: 12px;
    font-weight: 600;
    color: rgb(49, 48, 48);
  }
  #navbar-right_shrink > h6 > span {
    font-size: 13px;
    font-weight: 600;
    color: rgb(19, 68, 124);
  }

  #navbar-right_shrink > h6 {
    font-size: 12px;
    font-weight: 600;
    color: rgb(19, 68, 124);
  }
}

@media screen and (max-width: 700px) {
  .profile_pic {
    border-radius: 50%;
    width: 29%;
    border: 2px solid white;
    transition: 0.4s;
  }
}

@media screen and (max-width: 550px) {
  .profile_pic_shrink {
    border-radius: 50%;
    height: 90px;
    width: 90px;
    border: 2px solid white;
    transition: 0.4s;
  }
  .profile_pic {
    border-radius: 50%;
    height: 120px;
    width: 120px;
    border: 2px solid white;
    transition: 0.4s;
  }
  #navbar-right > h4 {
    font-size: 25px;
  }

  #navbar-right > h5 {
    font-size: 14px;
    font-weight: 600;
    color: rgb(49, 48, 48);
  }
  #navbar-right > h6 > span {
    font-size: 15px;
    font-weight: 600;
    color: rgb(19, 68, 124);
  }

  #navbar-right > h6 {
    font-size: 14px;
    font-weight: 600;
    color: rgb(19, 68, 124);
  }
}
